<template>
  <div style="height: 100%; width: 100%; overflow: hidden;">

    <div v-if="utils.is_industry_valid(this.industry)" style="height: 100%; width: 100%; overflow: hidden;">

      <div v-if="industry != null" style="height: 100%; width: 100%; overflow: hidden;">
        <v-row style="height: 100%; width: 100%; overflow: hidden; margin-top: 5px">
          <v-col cols="5" style="height: 100%; width: 100%;">
            <v-btn-toggle
              v-model="toggle"
              tile
              mandatory
              variant="outlined"
              divided
              style="color: #b62373; width: 100%"
            >
              <v-btn
                  tile
                  style="background-color:#b62373; border:none; width: 70%; font-size: min(1vw, 15px)"
              >
                IMPACT AND LEVERS FOR ACTION
              </v-btn>
              <v-btn
                tile
                style="background-color:#b62373; border:none; width: 30%; font-size: min(1vw, 15px)"
              >
                CONTEXT
              </v-btn>

            </v-btn-toggle>
            <!--<v-tabs
                id="main_tab"
                v-model="main_tab"
                style="width: 100%"
            >
              <v-tabs-slider color="#b62373"></v-tabs-slider>

              <v-tab style="border-color: #b62373">CONTEXT</v-tab>
              <v-tab style="border-color: #b62373">IMPACT AND LEVERS FOR ACTION</v-tab>


            </v-tabs>-->
            <div style="height: 92%; overflow-y: auto; width: 100%;">
              <v-treeview
                  :items="layer_tree"
                  @update:active="layerTreeSelected"
                  open-all
                  dense
                  hoverable
                  return-object
                  color="#1C195B"
                  selected-color="#1C195B"
                  style="padding-left: 15px; height: 100%;"
                  item-disabled="locked"

                  open-on-click
                  activatable

                  v-if="toggle == 1"

              >
                <template v-slot:append="{ item }">
                  <v-tooltip bottom v-if="item.layer && item.layer.info" max-width="700px">
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon
                          color='#1C195B'
                          style="padding-right: 10px"
                          v-bind="attrs"
                          v-on="on"
                      >
                        mdi-information-outline
                      </v-icon>
                    </template>
                    <span v-html="item.layer.info"></span>
                  </v-tooltip>
                  <span v-if="item.name == 'Change in the state of Nature'" class="state_of_nature"></span>

                </template>

              </v-treeview>
              <v-treeview
                  :active.sync="active_indicator"
                  :items="indicators_industry"
                  open-all
                  activatable
                  open-on-click
                  transition
                  dense
                  v-else
                  style="padding-left: 15px; height: 100%;"

              >
                <template v-slot:prepend="{ item, open }"
                          style="height: 120%; margin-top: -10px; padding-bottom: -30px;">
                  <v-tooltip left v-if="indicator_risk_class(item) != null">
                    <template v-slot:activator="{ on, attrs }">
                      <div
                          style="width: 5px;"
                          :class="indicator_risk_class(item)[0]"
                          v-bind="attrs"
                          v-on="on"
                      >
                        &nbsp
                      </div>

                    </template>
                    <span>
                      {{ indicator_risk_class(item)[1] }}
                    </span>
                  </v-tooltip>
                </template>
                <template v-slot:label="{ item }">
                  <span v-if="item.name == 'Change in the state of Nature'" class="state_of_nature">
                    {{ item.name }}
                  </span>
                  <span v-else>
                    {{ item.name }}
                  </span>
                </template>

                <template v-slot:append="{ item }">
                  <v-tooltip bottom max-width="700px" v-if="item.info">
                    <template v-slot:activator="{ on, attrs }">
                      <v-icon
                          color='#1C195B'
                          style="padding-right: 10px"
                          v-bind="attrs"
                          v-on="on"
                      >
                        mdi-information-outline
                      </v-icon>
                    </template>
                    <span>{{ item.info }}</span>
                  </v-tooltip>
                </template>

              </v-treeview>

            </div>

          </v-col>


          <v-col cols = 7 style="height: 99%; width: 100%; overflow-y: auto;">
            <div style="padding: 10px 50px 50px 50px">
              <v-hover v-slot="{ hover }">
                <v-alert
                    shaped
                    type="warning"
                    prominent
                    border="left"
                    @click="set_pollutants_factors"
                    :outlined = "!hover"
                    v-if="pollutants_without_factor"
                >
                  You have some conversion factors (to calculate some statistics) unset. Click here to set them.
                </v-alert>
              </v-hover>

              <v-hover v-slot="{ hover }">
                <v-alert
                    shaped
                    type="warning"
                    prominent
                    border="left"
                    :outlined = "!hover"
                    @click="edit_industry"
                    v-if="!utils.industry_has_all_inputs(industry)"
                >
                  The industry has some mandatory entries that are not configured, and the information shown here may not be completely accurate. Click here to edit the industry.                </v-alert>
              </v-hover>

              <div v-if="toggle == 1">
                <h3>
                  <span style="color: #b62373;">{{industry.name}}</span>: Context layers
                </h3>
                <v-data-table
                    :headers="industry_table.header"
                    :items="industry_table.value"
                    :hide-default-footer="true"
                    :loading = "loading_context_table"
                    loading-text="Loading... Please wait"
                    :custom-sort="customSort"
                >

                  <template v-slot:item.type="{ item }">
                    <v-tooltip left v-if="item.supplier_name == industry.name">
                      <template v-slot:activator="{ on, attrs }">
                        <v-icon
                            v-bind="attrs"
                            v-on="on"
                        >
                          mdi-factory
                        </v-icon>
                      </template>
                      <span>Main industry</span>
                    </v-tooltip>
                    <v-tooltip left v-else>
                      <template v-slot:activator="{ on, attrs }">
                        <v-icon
                            v-bind="attrs"
                            v-on="on"
                        >
                          mdi-truck
                        </v-icon>
                      </template>
                      <span>Supplier</span>
                    </v-tooltip>
                  </template>

                  <template v-slot:header.overall_water_risk="{ header }">
                    {{ header.text }}

                    <v-tooltip bottom max-width="700px">
                      <template v-slot:activator="{ on, attrs }">
                        <v-icon
                            small
                            v-bind="attrs"
                            v-on="on"
                        >mdi-information-outline</v-icon>
                      </template>
                      <span>
                      Overall water risk measures all water-related risks of an industry, by aggregating all indicators from the Physical Quantity, Quality and Regulatory & Reputational Risk categories. Higher values indicate higher water risk.
                      <br>
                      <b>Source: Aqueduct</b>

                    </span>
                    </v-tooltip>


                  </template>
                  <template v-slot:header.baseline="{ header }">
                    {{ header.text }}

                    <v-tooltip bottom max-width="700px">
                      <template v-slot:activator="{ on, attrs }">
                        <v-icon
                            small
                            v-bind="attrs"
                            v-on="on"
                        >mdi-information-outline</v-icon>
                      </template>
                      <span>
                      {{selected_layer.layer.layers.baseline.annual.layer["unit"]()}}
                    </span>
                    </v-tooltip>


                  </template>
                  <template v-slot:header.future="{ header }">
                    {{ header.text }}

                    <v-tooltip bottom max-width="700px">
                      <template v-slot:activator="{ on, attrs }">
                        <v-icon
                            small
                            v-bind="attrs"
                            v-on="on"
                        >mdi-information-outline</v-icon>
                      </template>
                      <span>
                      {{selected_layer.layer.layers.baseline.annual.layer["unit"]()}}
                    </span>
                    </v-tooltip>


                  </template>

                  <template
                      v-slot:item.overall_water_risk="{ item }"
                  >
                    <template v-if="get_owr_color(item) != null">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <v-chip
                              :color="get_owr_color(item)[0]"
                              dark
                              v-bind="attrs"
                              v-on="on"
                              text-color="#1c1c1b"
                          >
                            {{ item['overall_water_risk'] }}
                          </v-chip>
                        </template>
                        <span>{{ get_owr_color(item)[1] }}</span>
                      </v-tooltip>
                    </template>
                    <template v-else>
                      <v-chip
                          color="transparent"
                          dark
                          text-color="#1c1c1b"
                          class= "chip_no_hover"
                      >
                        {{ item['overall_water_risk'] }}
                      </v-chip>
                    </template>
                  </template>

                  <template
                      v-for="value in ['baseline', 'future']"
                      v-slot:[`item.${value}`]="{ item }"
                  >
                    <template v-if="getGISLayerColor(item, value) != null">
                      <v-tooltip bottom>
                        <template v-slot:activator="{ on, attrs }">
                          <v-chip
                              :color="getGISLayerColor(item, value)[0]"
                              dark
                              :key="value"
                              v-bind="attrs"
                              v-on="on"
                              text-color="#1c1c1b"
                          >
                            {{ item[value] }}
                          </v-chip>
                        </template>
                        <span>{{ getGISLayerColor(item, value)[1] }}</span>
                      </v-tooltip>
                    </template>
                    <template v-else>
                      <v-chip
                          color="transparent"
                          dark
                          :key="value"
                          text-color="#1c1c1b"
                          class= "chip_no_hover"
                      >
                        {{ item[value] }}
                      </v-chip>
                    </template>
                  </template>
                </v-data-table>
                <BarChart
                    v-if="show_context_chart"
                    style="padding-top: 40px;"
                    :chart-data="context_chart.chartData"
                    :chart-options="context_chart.chartOptions"
                />
              </div>
              <div v-else>
                <h3>
                  <span style="color: #b62373">{{industry.name}}</span>: evaluation of impacts of industrial wastewater
                </h3>
                <div
                    class="text-h6 grey--text text--lighten-1 font-weight-light"
                    style="align-self: center; margin-top: 20px"
                    v-if="active_indicator.length == 0"
                >
                  Select an indicator
                </div>
                <div v-else style="width: 100%; margin-top: 20px">
                  <div style="width: 100%">
                    <div v-if="active_indicator[0] == 3">

                      <v-chip-group
                          mandatory
                          v-model="delta_ecotox_chip"
                      >
                        <v-chip
                            color="primary"
                            outlined
                            class="ma-2"
                            pill
                        >
                          <v-icon left>
                            mdi-chart-pie
                          </v-icon>
                          Chart
                        </v-chip>

                        <v-chip
                            color="primary"
                            outlined
                            pill
                            class="ma-2"
                        >
                          <v-icon left>
                            mdi-table
                          </v-icon>
                          Table

                        </v-chip>
                      </v-chip-group>
                      <v-data-table
                          :headers="delta_ecotox_table.header"
                          :items="delta_ecotox_table.value"
                          :item-class="itemRowBold"
                          :hide-default-footer="true"
                          disable-pagination
                          dense
                          v-if="delta_ecotox_chip === 1"

                      >
                        <template v-slot:item.name="{ item }">
                        <span v-if="item.info">
                          {{ item.name }}
                          <v-btn
                            icon

                            @click="selected_pollutant = item.name; $data[item.info] = true"
                            class="icon_clickable"
                            x-small
                        >
                          <v-icon
                              color='#1C195B'
                          >
                            mdi-information-outline
                          </v-icon>
                        </v-btn>
                        </span>
                          <span v-else>{{ item.name }}</span>
                        </template>

                        <template
                            v-slot:item.value="{ item }"
                        >
                          <template v-if="getDeltaEcotoxColor(item) != null">
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <v-chip
                                    :color="getDeltaEcotoxColor(item)[0]"
                                    dark
                                    :key="industry.name"
                                    v-bind="attrs"
                                    v-on="on"
                                    text-color="#1c1c1b"
                                >
                                  {{ item.value }}
                                </v-chip>
                              </template>
                              <span>{{ getDeltaEcotoxColor(item)[1] }}</span>
                            </v-tooltip>
                          </template>
                          <template v-else>
                            <v-chip
                                color="transparent"
                                dark
                                :key="industry.name"
                                text-color="#1c1c1b"
                                class="chip_no_hover"
                            >
                              {{ item.value }}
                            </v-chip>
                          </template>

                        </template>
                        <template
                            v-slot:item.data="{ item }"
                        >
                          <template v-if="getDataTypeColor(item) != null">
                            <v-tooltip bottom max-width="300px">
                              <template v-slot:activator="{ on, attrs }">
                                <v-chip
                                    :color="getDataTypeColor(item)[0]"
                                    dark
                                    :key="industry.name"
                                    text-color="#1c1c1b"
                                    v-bind="attrs"
                                    v-on="on"
                                >
                                  {{ item.data }}
                                </v-chip>
                              </template>
                              <span>{{getDataTypeColor(item)[1]}}</span>
                            </v-tooltip>
                          </template>
                        </template>

                      </v-data-table>

                      <div v-else-if="delta_ecotox_chip === 0">
                        <p class="chart_no_data" v-if="utils.get_pollutants([this.industry]).length == 0">
                          Current industry doesn't have pollutants selected
                        </p>
                        <div v-else>
                          <PieChart
                              style="padding-top: 10px"
                              :chart-data="delta_ecotox_chart.chartData"
                              :chart-options="delta_ecotox_chart.chartOptions"

                          />
                          <p class="instructions_2">
                            *Only labels >5% are shown.
                          </p>
                        </div>
                      </div>
                    </div>
                    <div v-else-if="active_indicator[0] == 4">
                      <v-chip-group
                          mandatory
                          v-model="delta_eqs_chip"
                      >
                        <v-chip
                            color="primary"
                            outlined
                            class="ma-2"
                            pill
                        >
                          <v-icon left>
                            mdi-chart-pie
                          </v-icon>
                          Chart
                        </v-chip>

                        <v-chip
                            color="primary"
                            outlined
                            pill
                            class="ma-2"
                        >
                          <v-icon left>
                            mdi-table
                          </v-icon>
                          Table

                        </v-chip>
                      </v-chip-group>
                      <div v-if="delta_eqs_chip == 0">
                        <p class="chart_no_data" v-if="utils.get_pollutants([this.industry]).length == 0">
                          Current industry doesn't have pollutants selected
                        </p>
                        <BarChart
                            style="padding-top: 40px;"
                            :chart-data="delta_eqs_chart.chartData"
                            :chart-options="delta_eqs_chart.chartOptions"
                            v-else

                        />
                      </div>

                      <v-data-table
                          :headers="delta_eqs_table.header"
                          :items="delta_eqs_table.value"
                          v-else

                          disable-pagination
                          :hide-default-footer="true"
                          dense
                      >

                        <template v-slot:item.name="{ item }">
                          <span v-if="item.info">
                            {{ item.name }}
                            <v-btn
                                icon
                                @click="selected_pollutant = item.name; $data[item.info] = true"
                                class="icon_clickable"
                                x-small
                            >
                              <v-icon
                                  color='#1C195B'
                              >
                                mdi-information-outline
                              </v-icon>
                            </v-btn>
                          </span>
                          <span v-else>{{ item.name }}</span>
                        </template>
                        <template
                            v-slot:item.value="{ item }"
                        >
                          <template v-if="getDeltaEQSColor(item) != null">
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <v-chip
                                    :color="getDeltaEQSColor(item)[0]"
                                    dark
                                    :key="industry.name"
                                    v-bind="attrs"
                                    v-on="on"
                                    text-color="#1c1c1b"
                                >
                                  {{ item.value }}
                                </v-chip>
                              </template>
                              <span>{{ getDeltaEQSColor(item)[1] }}</span>
                            </v-tooltip>
                          </template>
                          <template v-else>
                            <v-chip
                                color="transparent"
                                dark
                                :key="industry.name"
                                text-color="#1c1c1b"
                                class="chip_no_hover"
                            >
                              {{ item.value }}
                            </v-chip>
                          </template>
                        </template>
                        <template
                            v-slot:item.data="{ item }"
                        >
                          <template v-if="getDataTypeColor(item) != null">
                            <v-tooltip bottom max-width="300px">
                              <template v-slot:activator="{ on, attrs }">
                                <v-chip
                                    :color="getDataTypeColor(item)[0]"
                                    dark
                                    :key="industry.name"
                                    text-color="#1c1c1b"
                                    v-bind="attrs"
                                    v-on="on"
                                >
                                  {{ item.data }}
                                </v-chip>
                              </template>
                              <span>{{getDataTypeColor(item)[1]}}</span>
                            </v-tooltip>
                          </template>
                        </template>

                      </v-data-table>

                    </div>
                    <div v-else-if="active_indicator[0] == 5">

                      <v-chip-group
                          mandatory
                          v-model="eutrophication_chip"
                      >
                        <v-chip
                            color="primary"
                            outlined
                            class="ma-2"
                            pill
                        >
                          <v-icon left>
                            mdi-chart-pie
                          </v-icon>
                          Chart
                        </v-chip>

                        <v-chip
                            color="primary"
                            outlined
                            pill
                            class="ma-2"
                        >
                          <v-icon left>
                            mdi-table
                          </v-icon>
                          Table

                        </v-chip>
                      </v-chip-group>
                      <v-data-table
                          :headers="eutrophication_table.header"
                          :items="eutrophication_table.value"
                          :item-class="itemRowBold"
                          disable-pagination
                          :hide-default-footer="true"
                          dense
                          v-if="eutrophication_chip === 1"
                      >
                        <template v-slot:item.name="{ item }">
                      <span v-if="item.info">
                        {{ item.name }}
                        <v-btn
                            icon
                            @click="selected_pollutant = item.name; $data[item.info] = true"
                            class="icon_clickable"
                            x-small
                        >
                          <v-icon
                              color='#1C195B'
                          >
                            mdi-information-outline
                          </v-icon>
                        </v-btn>

                      </span>
                          <span v-else>{{ item.name }}</span>
                        </template>

                        <template
                            v-slot:item.value="{ item }"
                        >

                          <template v-if="getEutrophicationColor(item) != null">
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <v-chip
                                    :color="getEutrophicationColor(item)[0]"
                                    dark
                                    :key=industry.name
                                    v-bind="attrs"
                                    v-on="on"
                                    text-color="#1c1c1b"
                                >
                                  {{ item.value }}
                                </v-chip>
                              </template>
                              <span>{{ getEutrophicationColor(item)[1] }}</span>
                            </v-tooltip>
                          </template>
                          <template v-else>
                            <v-chip
                                color="transparent"
                                dark
                                :key="industry.name"
                                text-color="#1c1c1b"
                                class="chip_no_hover"
                            >
                              {{ item.value }}
                            </v-chip>
                          </template>
                        </template>
                        <template
                            v-slot:item.data="{ item }"
                        >
                          <template v-if="getDataTypeColor(item) != null">
                            <v-tooltip bottom max-width="300px">
                              <template v-slot:activator="{ on, attrs }">
                                <v-chip
                                    :color="getDataTypeColor(item)[0]"
                                    dark
                                    :key="industry.name"
                                    text-color="#1c1c1b"
                                    v-bind="attrs"
                                    v-on="on"
                                >
                                  {{ item.data }}
                                </v-chip>
                              </template>
                              <span>{{getDataTypeColor(item)[1]}}</span>
                            </v-tooltip>
                          </template>
                        </template>

                      </v-data-table>

                      <div v-else-if="eutrophication_chip === 0">
                        <PieChart
                            style="padding-top: 10px"
                            :chart-data="eutrophication_chart.chartData"
                            :chart-options="eutrophication_chart.chartOptions"

                        />
                        <p class="instructions_2">
                          *Only labels >5% are shown.
                        </p>
                      </div>

                    </div>
                    <div v-else-if="active_indicator[0] == 6">

                      <v-data-table
                          :headers="delta_temperature_table.header"
                          :items="delta_temperature_table.value"
                          :item-class="itemRowBold"
                          disable-pagination
                          :hide-default-footer="true"
                          dense
                      >
                        <template v-slot:item.name="{ item }">
                      <span v-if="item.info">
                        {{ item.name }}
                        <v-btn
                            icon
                            class="icon_clickable"
                            x-small
                            @click="$data[item.info] = true"

                        >
                          <v-icon
                              color='#1C195B'
                          >
                            mdi-information-outline
                          </v-icon>
                        </v-btn>

                      </span>
                          <span v-else>{{ item.name }}</span>
                        </template>

                        <template
                            v-slot:item.value="{ item }"
                        >

                          <template v-if="getDeltaTemperatureColor(item) != null">
                            <v-tooltip bottom>
                              <template v-slot:activator="{ on, attrs }">
                                <v-chip
                                    :color="getDeltaTemperatureColor(item)[0]"
                                    dark
                                    :key=industry.name
                                    v-bind="attrs"
                                    v-on="on"
                                    text-color="#1c1c1b"
                                >
                                  {{ item.value }}
                                </v-chip>
                              </template>
                              <span>{{ getDeltaTemperatureColor(item)[1] }}</span>
                            </v-tooltip>
                          </template>
                          <template v-else>
                            <v-chip
                                color="transparent"
                                dark
                                :key="industry.name"
                                text-color="#1c1c1b"
                                class="chip_no_hover"
                            >
                              {{ item.value }}
                            </v-chip>
                          </template>
                        </template>
                        <template
                            v-slot:item.data="{ item }"
                        >
                          <template v-if="getDataTypeColor(item) != null">
                            <v-tooltip bottom max-width="300px">
                              <template v-slot:activator="{ on, attrs }">
                                <v-chip
                                    :color="getDataTypeColor(item)[0]"
                                    dark
                                    :key="industry.name"
                                    text-color="#1c1c1b"
                                    v-bind="attrs"
                                    v-on="on"
                                >
                                  {{ item.data }}
                                </v-chip>
                              </template>
                              <span>{{getDataTypeColor(item)[1]}}</span>
                            </v-tooltip>
                          </template>
                        </template>

                      </v-data-table>

                    </div>

                    <div v-else-if="active_indicator[0] == 9">
                      <v-data-table
                          :headers="treatment_efficiency_table.header"
                          :items="treatment_efficiency_table.value"
                          disable-pagination
                          :hide-default-footer="true"
                          dense
                      >

                        <template v-slot:item.name="{ item }">
                          <span v-if="item.info">
                            {{ item.name }}
                            <v-btn
                                icon
                                @click="selected_pollutant = item.name; $data[item.info] = true"
                                class="icon_clickable"
