          //text: key, value: key,
        //})
        //pollutants_table.header.push({text: "Unit", value: "unit", sortable: false,})

        //let dichloroethane = {value: _this.table_title.pollutants.diclo, unit: "%", info: "info_delta_eqs_diclo"}
        let tu = await metrics.delta_eqs(industries, _this.global_layers, true)

        let labels_dataset = []
        let values_dataset = []
        for(let pollutant of utils.remove_nutrients(this.industry.pollutants_selected)){
          let DataType = industry_impact_legend_category.delta_eqs(industries[0], pollutant)
          let pollutant_obj = {
            name: pollutant,
            unit: "%",
            info: "info_delta_eqs",
            value: tu[pollutant],
            data: this.get_string_impact_legend(DataType)
          }
          //pollutant_obj[key] = tu[pollutant]
          pollutants_table.value.push(pollutant_obj)

          labels_dataset.push(pollutant)
          values_dataset.push(tu[pollutant])
        }

        this.delta_eqs_chart = {
          chartData: {
            labels: labels_dataset,
            datasets: [
              {
                data: values_dataset,
                backgroundColor: labels_dataset.map(pollutant => this.chooseColor(pollutant)),
              },

            ]
          },
          chartOptions: {
            responsive: true,
            maintainAspectRatio: false,
            plugins: {
              legend: {
                display: false
              },
              tooltip: {
                callbacks: {
                  label: function(context) {
                    return context.label + ": "+context.raw + "%"
                  },
                }

              },
            },
            scales: {
              y: {
                ticks: {
                  beginAtZero:true,
                  callback: function(value, index, values) {
                    return value + '%';
                  }
                },
              }
            }
          },

        }


        return pollutants_table
      } else return {header: [], emissions: []}

    },

    //"Increase in toxic units in the receiving water body after discharge" table
    async generate_delta_ecotox_table() {

      let _this = this

      if (_this.industry !== null) {

        let industries = [this.industry]

        let pollutants_table = {
          header: [{text: "", value: "name", sortable: false},
            {text: this.industry.name, value: "value"},
            {text: "Unit", value: "unit", sortable: false},
            {text: "Data type", value: "data", sortable: false}
          ],
          value: []
        }

        let tu = await metrics.delta_tu(industries, _this.global_layers, true)

        let labels_dataset = []
        let values_dataset = []

        if (utils.remove_nutrients(this.industry.pollutants_selected).length > 0){
          let total = {name: _this.table_title.pollutants.total, unit: "TU/day", value: tu.total}
          pollutants_table.value.push(total)

          for(let pollutant of utils.remove_nutrients(this.industry.pollutants_selected)){
            let DataType = industry_impact_legend_category.delta_tu(industries[0], pollutant)
            let pollutant_obj = {
              name: pollutant,
              unit: "TU/day",
              info: "info_delta_tu",
              value: tu[pollutant],
              data: this.get_string_impact_legend(DataType)

            }
            pollutants_table.value.push(pollutant_obj)

            labels_dataset.push(pollutant)
            values_dataset.push(tu[pollutant])
          }
        }




        //let dichloroethane = {value: _this.table_title.pollutants.diclo, unit: "TU/day", info: "info_delta_tu_diclo"}


        _this.delta_ecotox_chart = {
          chartData: {
            labels: labels_dataset,
            datasets: [
              {
                backgroundColor: labels_dataset.map(pollutant => this.chooseColor(pollutant)),
                data: values_dataset
              }
            ]
          },
          chartOptions: {
            responsive: true,
            maintainAspectRatio: false,
            plugins: {
              tooltip: {
                callbacks: {
                  label: function(context) {
                    let datasets = context.dataset;
                    let sum = datasets.data.map(x => parseFloat(x)).reduce((a, b) => a + b, 0);
                    let percentage = ((context.raw / sum) * 100).toFixed(2);

                    return context.label + ": "+context.raw + " TU/day" + " ("+percentage+"%)"
                  }
                }

              },
              datalabels: {
                formatter: function (value, ctx) {
                  let datasets = ctx.chart.data.datasets;
                  if (datasets.length > 0) {
                    let sum = datasets[0].data.map(x => parseFloat(x)).reduce((a, b) => a + b, 0);
                    let percentage = ((value / sum) * 100).toFixed(2);
                    if(percentage > 5) {
                      return percentage + "%"
                    }else return ''
                  }

                },
                color: 'white'
              },
            },
          }
        }

        return pollutants_table
      } else return {header: [], emissions: []}

    },

