    //Default context table
    async generate_industry_table() {

      let _this = this

      if (_this.industry !== null) {
        let table = {
          header: [
            {text: "Type", value: "type"},
              {text: "Name", value: "supplier_name"}, {
            text: "Country",
            value: "country"
          }, {text: "Latitude", value: "lat"}, {text: "Longitude", value: "lng"}, {text: "Overall water risk", value: "overall_water_risk"}],
          value: []
        }

        let arr =  [this.industry, ...this.industry.supply_chain]

       for (let supply_chain of arr){

         //calculate overall water risk index
         let owr = await utils.overall_water_risk(supply_chain.location.lat, supply_chain.location.lng)
         owr = owr.toFixed(3)
         table.value.push({
            supplier_name: supply_chain.name,
            lat: supply_chain.location.lat.toFixed(3),
            lng: supply_chain.location.lng.toFixed(3),
            country: utils.get_country_code_from_coordinates(supply_chain.location.lat, supply_chain.location.lng),
            overall_water_risk: owr
          })
        }
       return table
      } else return {header: [], value: []}

    },

    async generate_context_for_impact_table(new_value, old_value){
      if(!(((new_value >= 1 && new_value <= 13) && (old_value >= 1 && old_value <=13)) || ((new_value >= 14 && new_value <= 16) && (old_value >= 14 && old_value <= 16)))) {
        let _this = this
        let table = {
          header: [
            {text: "Indicator", value: "indicator", sortable: false},
            //{text: "Overall water risk", value: "risk", sortable: false},
            {text: "Baseline", value: "baseline", sortable: false},
            {text: "Future", value: "future", sortable: false},
            {text: "Units", value: "unit", sortable: false}],
          value: []
        }

        let indicators = []

        //indicators for water quality
        if (_this.active_indicator >= 1 && _this.active_indicator <= 13) {
          indicators = ["Coastal Eutrophication Potential", "Unimproved/No Drinking Water", "Unimproved/No Sanitation", "BOD", "Nitrates", "Streamflow"]
        }
        //indicators for water availability
        else if (_this.active_indicator >= 14 && _this.active_indicator <= 16) {
          indicators = ["Seasonal variability", "Interannual variability", "Water stress", "Water depletion", "Aridity index", "Groundwater table decline", "Drought risk", "Streamflow"]
        }
        //ghg emissions does not have context indicators related

        for (let indicator of indicators) {
          let location = _this.industry.location
          //let risk = await utils.overall_water_risk(location.lat, location.lng)
          //risk = risk.toFixed(3)
          let baseline = await _this.global_layers[indicator].layers.baseline.annual.layer.data_for_report(location.lat, location.lng)
          let future = "-"
          if (_this.global_layers[indicator].future == true) {
            future = await _this.global_layers[indicator].layers.future.layer.data_for_report(location.lat, location.lng)
          }
          let unit = _this.global_layers[indicator].layers.baseline.annual.layer.unit()
          table.value.push({
            indicator: indicator,
            //risk: risk,
            baseline: baseline,
            future: future,
            unit: unit

          })
          this.context_for_impact_table = table
        }
      }
    },

    /*Gets the string equivalent to the input
      UD = User Data = 1
      Mo = Modeled = 2
      Es = Estimated = 3
      ID = Insufficient Data = 4
     */
    get_string_impact_legend(value){
      return utils.get_string_impact_legend(value)
    },

